<template>
    <div class="d-flex">
      
        <v-avatar size="30" v-if="$getNestedValue(item, header.value)">
            <v-img :src="getImageSrc(profileImage)"></v-img>
        </v-avatar>
        <span class="align-self-center mx-1">
            {{$options.filters.dynamic([item, header.value], header.format, getFormatArgs(header.formatArgs, item) || [])}}  
        </span>
    </div>
</template>
<script>
export default {
    name:'TableFieldUser',
    props: {
        item: {
            type: Object,
        },
        header: {
            type: Object
        },
        img: String //profile image field 
    },
    computed: {
        profileImage () {

            if (this.img) {
                return this.$getNestedValue(this.item, this.img)
            } else if (this.item.user)
                return this.item.user.profileImage
            else 
                return this.item.profileImage 
        }
    }
}
</script>

